import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, ElementRef, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, FormsModule, ReactiveFormsModule, Validators } from '@angular/forms';
import { environment } from '../../../../environments/environment';
import { CommonModule } from '@angular/common';
declare var bootstrap: any;

export interface Blog {
  id: number;
  title: string;
  description: string;
  location: string;
  date: string;
  url: string;
  createdAt: string;
  updatedAt: string;
  isActive: boolean;
}
@Component({
  selector: 'app-publications',
  standalone: true,
  imports: [CommonModule, FormsModule, ReactiveFormsModule],
  templateUrl: './publications.component.html',
  styleUrl: './publications.component.scss'
})
export class PublicationsComponent {
  blogForm: FormGroup;
  previewUrl: string | ArrayBuffer | null = null;
  searchText: string = '';
  pageSize: number = 5;
  currentPage: number = 1;
  totalPages: number = 1;   // <-- make it a property
  toastMessage: string = '';
  blogs: Blog[] = [];
  editEventForm!: FormGroup;
  selectedEventId: number | null = null;
  @ViewChild('errorToast', { static: false }) errorToast!: ElementRef;
  today: string = new Date().toISOString().split('T')[0];

  constructor(private fb: FormBuilder, private http: HttpClient) {
    this.blogForm = this.fb.group({
      title: [''],
      date: [''],
      description: [''],
      announcements: [''],
      venue: [''],
      notifyAll: [false],
      videolink: ['']
    });
  }

  ngOnInit(): void {
    this.loadBlogs();

    this.editEventForm = this.fb.group({
      title: ['', [Validators.required]],
      description: ['', [Validators.required]]
    });
  }

  openEditDrawer(blog: Blog): void {
    this.selectedEventId = blog.id;
    this.editEventForm.patchValue({
      title: blog.title,
      description: blog.description
    });

    const offcanvasElement = document.getElementById('editEventDrawer');
    if (offcanvasElement) {
      const bsOffcanvas = new bootstrap.Offcanvas(offcanvasElement);
      bsOffcanvas.show();
    }
  }

  onUpdateEvent(): void {
    if (this.editEventForm.invalid || !this.selectedEventId) return;

    const token = localStorage.getItem('authToken');
    const headers = new HttpHeaders({
      Authorization: `Bearer ${token}`
    });

    this.http.patch(
      `${environment.apiUrl}/events/${this.selectedEventId}`,
      this.editEventForm.value,
      { headers }
    ).subscribe({
      next: (res) => {
        console.log('Event updated:', res);
        this.showSuccessToast('Event updated successfully!');
        this.loadBlogs(); // ðŸ”¹ Refresh list
        const offcanvasEl = document.getElementById('editEventDrawer');
        if (offcanvasEl) {
          const bsOffcanvas = bootstrap.Offcanvas.getOrCreateInstance(offcanvasEl);
          bsOffcanvas.hide();
          setTimeout(() => {
            document.querySelectorAll('.offcanvas-backdrop')
              .forEach(el => el.remove());
            document.body.classList.remove('offcanvas-backdrop', 'show'); // just in case
          }, 100); // wait for animation
        }

      },
      error: (err) => {
        console.error('Failed to update event:', err);
        this.showErrorToast('Failed to update event');
      }
    });
  }

  loadBlogs(): void {
    const token = localStorage.getItem('authToken');
    if (!token) {
      this.showErrorToast('Auth token not found');
      return;
    }

    const headers = new HttpHeaders({
      'Authorization': `Bearer ${token}`,
      'Content-Type': 'application/json'
    });

    this.http.get<any>(`${environment.apiUrl}/events?page=${this.currentPage}&limit=${this.pageSize}&isActive=true`, { headers })
      .subscribe({
        next: (res) => {
          this.blogs = res.data.data;
          this.totalPages = res.data.pagination?.total
            ? Math.ceil(res.data.pagination.total / this.pageSize)
            : 1;
        },
        error: (err) => {
          console.error(err);
          this.showErrorToast('Failed to load blogs');
        }
      });
  }

  changePage(page: number) {
    if (page >= 1 && page <= this.totalPages) {
      this.currentPage = page;
      this.loadBlogs();
    }
  }

  goPrevious() {
    if (this.currentPage > 1) {
      this.currentPage--;
      this.loadBlogs();
    }
  }

  goNext() {
    if (this.currentPage < this.totalPages) {
      this.currentPage++;
      this.loadBlogs();
    }
  }

  onPageSizeChange(event: any) {
    this.pageSize = +event.target.value;
    this.currentPage = 1;
    this.loadBlogs();
  }

  get filteredBlogs(): Blog[] {
    const term = this.searchText.toLowerCase();
    let filtered = this.blogs.filter(blog =>
      blog.title.toLowerCase().includes(term) ||
      blog.description.toLowerCase().includes(term) ||
      blog.createdAt.toLowerCase().includes(term) ||
      blog.updatedAt.toLowerCase().includes(term)
    );

    const start = (this.currentPage - 1) * this.pageSize;
    const end = start + this.pageSize;
    return filtered.slice(start, end);
  }

  onSubmit() {
    if (this.blogForm.invalid) {
      this.showErrorToast('Please fill all fields and upload an image');
      return;
    }

    const token = localStorage.getItem('authToken');
    if (!token) {
      this.showErrorToast('Auth token not found');
      return;
    }

    const headers = new HttpHeaders({
      'Authorization': `Bearer ${token}`,
      'Content-Type': 'application/json'
    });

    const payload = {
      title: this.blogForm.value.title,
      description: this.blogForm.value.description,
      date: this.blogForm.value.date,
      location: this.blogForm.value.venue?.trim() ? this.blogForm.value.venue : "null",
      url: this.blogForm.value.videolink?.trim() ? this.blogForm.value.videolink : null,
      announcement: this.blogForm.value.announcements?.trim() ? this.blogForm.value.announcements : null,
      isSentNotificationToAll: this.blogForm.value.notifyAll ? true : false
    };


    this.http.post(`${environment.apiUrl}/events`, payload, { headers })
      .subscribe({
        next: () => {
          this.showSuccessToast('Event submitted successfully!');
          this.blogForm.reset();
          this.loadBlogs();
        },
        error: (err) => {
          if (err.error && err.error.errors && Array.isArray(err.error.errors)) {
            err.error.errors.forEach((e: any) => {
              this.showErrorToast(e.message);
            });
          } else if (err.error && err.error.message) {
            this.showErrorToast(err.error.message);
          } else {
            this.showErrorToast('Failed to submit Event');
          }
        }

      });
  }

  closeOffcanvas(id: string) {
    const el = document.getElementById(id);
    if (el) {
      const instance = bootstrap.Offcanvas.getInstance(el) || new bootstrap.Offcanvas(el);
      instance.hide();
    }

    // Clean leftover backdrops if any
    document.querySelectorAll('.offcanvas-backdrop')
      .forEach(backdrop => backdrop.remove());
  }

  onDeleteBlog(userId: number) {
  const payload = { isActive: false }; // mark as inactive instead of deleting

  this.http.patch(`${environment.apiUrl}/events/${userId}`, payload)
    .subscribe({
      next: () => {
        this.showSuccessToast('Event deleted successfully!');
        this.loadBlogs(); // Refresh list
      },
      error: (err) => {
        console.error('Failed to delete (deactivate) event', err);
        this.showErrorToast('Failed to delete event');
      }
    });
}


  showErrorToast(message: string): void {
    this.toastMessage = message;

    if (!this.errorToast) {
      console.warn('errorToast element is not available');
      return;
    }

    const toastElement = this.errorToast.nativeElement;
    toastElement.classList.remove('bg-success');
    toastElement.classList.add('bg-danger');
    const toast = new bootstrap.Toast(toastElement);
    toast.show();
  }

  showSuccessToast(message: string) {
    this.toastMessage = message;
    const toastElement = this.errorToast.nativeElement;
    toastElement.classList.remove('bg-danger');
    toastElement.classList.add('bg-success');
    const toast = new bootstrap.Toast(toastElement);
    toast.show();
  }
  
}
